<template>
  <div id="app">
    <v-app id="inspire">
      <v-data-table :headers="headers" :items="infos" class="elevation-1">
        <template v-slot:[`item.date`]="{ item }">
          {{ item.date.substring(0, 10) }}
        </template>

        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>가입 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>

            <select
              class="selectStd"
              v-model="selectedIndex"
              style="margin-right:5px; text-align-last:center;  vertical-align:middle; margin-bottom: 6px; "
            >
              <option
                v-for="(std, index) in standards"
                :key="index"
                :value="index"
                >{{ std }}</option
              >
            </select>
            <v-btn
              color="primary"
              dark
              class="mb-2"
              @click="searchRegistration"
            >
              검색
            </v-btn>

            <v-dialog v-model="dialog" max-width="500px">
              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.userId"
                          label="유저 ID (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.address"
                          label="주소 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.zipcode"
                          label="우편번호 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.date"
                          label="가입날짜 (필수)"
                          readonly
                        ></v-text-field>
                      </v-col>
                    </v-row>

                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.pickup"
                          label="기기수령방법 ( 0:배송 / 1:방문수령) (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <div v-if="editedItem.pickup == 0">
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field
                            v-model="editedItem.deliveryName"
                            label="수령인"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field
                            v-model="editedItem.deliveryPhone"
                            label="연락 가능한 전화번호"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field
                            v-model="editedItem.deliveryDemand"
                            label="배송 시 주의사항"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </div>
                    <div v-else>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field
                            v-model="editedItem.storeId"
                            label="방문매장 ID"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field
                            v-model="editedItem.pickupDate"
                            label="방문수령시간대"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </div>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.colorId"
                          label="기기색상 ID (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.giftId"
                          label="선물 ID (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.insuranceId"
                          label="보험 ID (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.newNumber"
                          label="희망 신규전화번호"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.planId"
                          label="요금제 ID (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <!-- 부가서비스 선택 -->
                    <v-row justify="center">
                      <v-dialog
                        v-model="selectdialog"
                        scrollable
                        max-width="300px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn color="primary" dark v-bind="attrs" v-on="on">
                            부가서비스 선택
                          </v-btn>
                        </template>
                        <v-card>
                          <v-card-title>부가서비스 리스트</v-card-title>
                          <v-divider></v-divider>
                          <v-card-text>
                            <v-container fluid>
                              <v-checkbox
                                v-for="(item, index) in services"
                                :key="index"
                                v-model="editedItem.serviceList"
                                :label="item.name"
                                :value="item.serviceId"
                                multiple
                              ></v-checkbox>
                            </v-container>
                          </v-card-text>
                          <v-divider></v-divider>
                          <v-card-actions>
                            <v-btn
                              color="blue darken-1"
                              text
                              @click="cancelSelect"
                            >
                              취소
                            </v-btn>
                            <v-btn
                              color="blue darken-1"
                              text
                              @click="selectdialog = false"
                            >
                              저장
                            </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </v-row>
                    <!-- 부가서비스 선택 end -->
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    취소
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    저장
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">삭제하시겠습니까?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >삭제</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <!-- 상세보기 -->
            <v-dialog
              v-model="detaildialog"
              fullscreen
              hide-overlay
              transition="dialog-bottom-transition"
            >
              <template v-slot:activator="{}"> </template>
              <v-card>
                <!-- 상세 페이지 props -->
                <registration-detail
                  :item="editedItem"
                  @close-detail="closeDetail"
                />
              </v-card>
            </v-dialog>
            <!-- 삭제 모달 상세보기 end -->

            <!-- 검색 -->
            <v-dialog v-model="searchDialogService" max-width="400px">
              <v-card>
                <v-card-title>
                  <span class="headline">부가서비스 선택</span>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <v-container fluid>
                    <v-checkbox
                      v-for="(item, index) in services"
                      :key="index"
                      v-model="searchSelectService"
                      :label="item.name"
                      :value="item.serviceId"
                    ></v-checkbox>
                  </v-container>
                </v-card-text>
                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeSearch"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="selectCondition(2)"
                    >선택</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="searchDialogDevice" max-width="400px">
              <v-card>
                <v-card-title>
                  <span class="headline">디바이스 선택</span>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <v-container fluid>
                    <v-checkbox
                      v-for="(item, index) in devices"
                      :key="index"
                      v-model="searchSelectDevice"
                      :label="item.name"
                      :value="item.deviceId"
                    ></v-checkbox>
                  </v-container>
                </v-card-text>
                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeSearch"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="selectCondition(3)"
                    >선택</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="searchDialogPlan" max-width="400px">
              <v-card>
                <v-card-title>
                  <span class="headline">요금제 선택</span>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <v-container fluid>
                    <v-checkbox
                      v-for="(item, index) in plans"
                      :key="index"
                      v-model="searchSelectPlan"
                      :label="item.name"
                      :value="item.planId"
                    ></v-checkbox>
                  </v-container>
                </v-card-text>
                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeSearch"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="selectCondition(1)"
                    >선택</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>

        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">
            mdi-delete
          </v-icon>
          <v-icon small class="mr-2" @click="detailItem(item)">
            mdi-card-search-outline
          </v-icon>
        </template>

        <template v-slot:no-data>
          <h1>데이터가 없습니다.</h1>
        </template>
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import Constant from "../../Constant";

import RegistrationDetail from "./registrationDetail";

export default {
  components: {
    RegistrationDetail,
  },
  data() {
    return {
      standards: ["전체", "요금제", "부가서비스", "디바이스"],
      selectedIndex: 0,
      selectdialog: false,

      //검색
      targetSpace: "",
      searchSelectService: 0,
      searchSelectDevice: 0,
      searchSelectPlan: 0,

      searchDialogService: false,
      searchDialogDevice: false,
      searchDialogPlan: false,

      dialog: false,
      dialogDelete: false,
      detaildialog: false,
      headers: [
        {
          text: "ID",
          align: "start",
          // sortable: false,
          value: "registrationId",
          width: "5%",
        },
        { text: "가입자명", value: "userName", width: "5%" },
        { text: "선택기기", value: "deviceName", width: "10%" },
        { text: "요금제", value: "planName", width: "10%" },
        { text: "기기수령(0:배송/1:방문)", value: "pickup", width: "15%" },
        { text: "주소", value: "address", width: "25%" },
        { text: "가입일자", value: "date", width: "10%" },
        { text: "수정/삭제/상세보기", value: "actions", width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        address: "",
        colorId: 0,
        colorName: "",
        date: "",
        deliveryDemand: "",
        deliveryName: "",
        deliveryPhone: "",
        deviceId: 0,
        deviceName: "",
        dividePay: 0,
        giftId: 0,
        giftName: "",
        insuranceId: 0,
        insuranceName: "",
        newNumber: "",
        pickup: 0,
        pickupDate: "",
        planId: 0,
        planName: "",
        registrationId: 0,
        serviceList: [],
        storeId: 0,
        storeName: "",
        userId: 0,
        userName: "",
        zipcode: "",
      },
      defaultItem: {
        address: "",
        colorId: 0,
        colorName: "",
        date: "",
        deliveryDemand: "",
        deliveryName: "",
        deliveryPhone: "",
        deviceId: 0,
        deviceName: "",
        dividePay: 0,
        giftId: 0,
        giftName: "",
        insuranceId: 0,
        insuranceName: "",
        newNumber: "",
        pickup: 0,
        pickupDate: "",
        planId: 0,
        planName: "",
        registrationId: 0,
        serviceList: [],
        storeId: 0,
        storeName: "",
        userId: 0,
        userName: "",
        zipcode: "",
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "가입 생성" : "가입 수정";
    },
    infos() {
      let temp = this.$store.state.registrationstore.registrations;
      return temp;
    },
    services() {
      return this.$store.state.servicestore.services;
    },
    plans() {
      return this.$store.state.planstore.plans;
    },
    devices() {
      return this.$store.state.devicestore.devices;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    detaildialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {

    this.$store.dispatch(Constant.GET_REGISTRATIONLIST);
    this.$store.dispatch(Constant.GET_SERVICELIST);
    this.$store.dispatch(Constant.GET_DEVICELIST);
    this.$store.dispatch(Constant.GET_PLANLIST);
  },

  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.editedIndex = item.registrationId;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = item.registrationId;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {

      this.$store.dispatch(Constant.REMOVE_REGISTRATION, {
        registrationId: this.editedIndex,
      });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.detaildialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    //저장 아니면 수정
    save() {

      if (
        this.editedItem.address == "" ||
        this.editedItem.date == "" ||
        this.editedItem.colorId == 0 ||
        this.editedItem.giftId == 0 ||
        this.editedItem.insuranceId == 0 ||
        (this.editedItem.pickup != 0 && this.editedItem.pickup != 1) ||
        this.editedItem.planId == 0 ||
        this.editedItem.userId == 0 ||
        this.editedItem.zipcode == ""
      ) {
        alert("필수입력사항을 입력하십시오.");
      } else {
        // 수정
        if (this.editedIndex > -1) {
          this.$store.dispatch(Constant.MODIFY_REGISTRATION, {
            registrationId: this.editedIndex,
            address: this.editedItem.address,
            date: this.editedItem.date,
            deliveryDemand: this.editedItem.deliveryDemand,
            deliveryName: this.editedItem.deliveryName,
            deliveryPhone: this.editedItem.deliveryPhone,
            deviceColorId: this.editedItem.colorId,
            giftId: this.editedItem.giftId,
            insuranceId: this.editedItem.insuranceId,
            newNumber: this.editedItem.newNumber,
            pickup: this.editedItem.pickup,
            pickupDate: this.editedItem.pickupDate,
            planId: this.editedItem.planId,
            serviceList: this.editedItem.serviceList,
            storeId: this.editedItem.storeId,
            userId: this.editedItem.userId,
            zipcode: this.editedItem.zipcode,
          });
        }
        this.close();
      }
    },
    closeDetail() {
      this.detaildialog = false;
    },
    cancelSelect() {
      this.editedItem.serviceList = [];
      this.selectdialog = false;
    },
    searchRegistration() {

      //전체
      if (this.selectedIndex == 0) {
        this.$store.dispatch(Constant.GET_REGISTRATIONLIST);
      }
      //요금제
      else if (this.selectedIndex == 1) {
        this.searchDialogPlan = true;
      }
      //부가서비스
      else if (this.selectedIndex == 2) {
        this.searchDialogService = true;
      }
      //디바이스
      else {
        this.searchDialogDevice = true;
      }

    },
    selectCondition(idx) {
      if (idx == 1) {
        //요금제
        this.$store.dispatch(Constant.SEARCH_REGISTRATION_BY_PLAN, {
          planId: this.searchSelectPlan,
        });
        this.searchDialogPlan = false;
      } else if (idx == 2) {
        //부가서비스
        this.$store.dispatch(Constant.SEARCH_REGISTRATION_BY_SERVICE, {
          serviceId: this.searchSelectService,
        });
        this.searchDialogService = false;
      } else if (idx == 3) {
        //디바이스
        this.$store.dispatch(Constant.SEARCH_REGISTRATION_BY_DEVICE, {
          deviceId: this.searchSelectDevice,
        });
        this.searchDialogDevice = false;
      }
    },
    closeSearch() {
      this.searchDialogService = false;
      this.searchDialogDevice = false;
      this.searchDialogPlan = false;
    },
  },
};
</script>

<style scoped>
.selectStd {
  height: 36px;
  border: 1px solid black;
  border-radius: 5px;
}
</style>
