import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
import multi from "../../multi-common.js";

Vue.use(Vuex);

const devicestore = {
  state: {
    devices: [],
    device: {},
  },

  actions: {

    //전체 기기 리스트 가져오기
    [Constant.GET_DEVICELIST]: (store) => {
      http
        .get("/bo-api/device/")
        .then((response) => {
          store.commit(Constant.GET_DEVICELIST, { devices: response.data });
        })
        .catch((exp) => alert("getDeviceList처리에 실패하였습니다!!" + exp));
    },

    // 기기 id로 기기 하나 가져오기
    [Constant.GET_DEVICE]: (store, payload) => {
      http
        .get("/bo-api/device/" + payload.deviceId)
        .then((response) => {
          store.commit(Constant.GET_DEVICE, { device: response.data });
        })
        .catch((exp) => alert("getDevice처리에 실패하였습니다." + exp));
    },



    //기기 수정, 추가
    [Constant.MODIFY_DEVICE]: (store, payload) => {
      http
        .put("/bo-api/device/", {
          deviceId: payload.deviceId,
          name: payload.name,
          model: payload.model,
          cpu: payload.cpu,
          display: payload.display,
          displayInch: payload.displayInch,
          camera: payload.camera,
          battery: payload.battery,
          memory: payload.memory,
          size: payload.size,
          weight: payload.weight,
          waterproof: payload.waterproof,
          manufacturer: payload.manufacturer,
          description: payload.description,
          descriptionImage: "",
          storageDescription1: payload.storageDescription1,
          storageDescription2: payload.storageDescription2,
          price: payload.price,
          date: payload.date,
        })
        .then((response) => {
          let fd = new FormData();
          fd.append("descriptionImage", payload.descriptionImage);
          fd.append("deviceId", response.data);
          multi
            .post(
              "/bo-api/device/description_image",
              fd
             
            )
            .then(() => {
              store.dispatch(Constant.GET_DEVICELIST);
            })
            .catch(function() {
            });
        })
        .catch((exp) => alert("기기정보 수정 처리에 실패하였습니다." + exp));
    },

 

    // 기기 삭제
    [Constant.REMOVE_DEVICE]: (store, payload) => {
      http
        .delete("/bo-api/device/" + payload.deviceId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_DEVICELIST);
        })
        .catch((exp) => alert("기기정보 삭제 처리에 실패하였습니다." + exp));
    },
    
  },

  mutations: {
    [Constant.GET_DEVICELIST]: (state, payload) => {
      for (let i = 0; i < payload.devices.length; i++) {
        payload.devices[i].date = payload.devices[i].date.substring(0, 10);
      }
      state.devices = payload.devices;
    },
    [Constant.GET_DEVICE]: (state, payload) => {
      payload.device.date = payload.device.date.substring(0, 10);
      state.device = payload.device;
    },
  },

  modules: {},
};

export default devicestore;
