import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const noticestore = {
  state: {
    notices: [],
    notice: {},
  },

  actions: {
    //전체 공지 리스트 가져오기
    [Constant.GET_NOTICELIST]: (store) => {
      http
        .get("/bo-api/notice/")
        .then((response) => {
          store.commit(Constant.GET_NOTICELIST, { notices: response.data });
        })
        .catch((exp) => alert("getNoticeList처리에 실패하였습니다!!" + exp));
    },

    // 공지 id로 선물 하나 가져오기
    [Constant.GET_NOTICE]: (store, payload) => {
      http
        .get("/bo-api/notice/" + payload.noticeId)
        .then((response) => {
          store.commit(Constant.GET_NOTICE, { notice: response.data });
        })
        .catch((exp) => alert("getNotice처리에 실패하였습니다." + exp));
    },

    //공지 수정
    [Constant.MODIFY_NOTICE]: (store, payload) => {
      http
        .put("/bo-api/notice/", {
          noticeId: payload.noticeId,
          content: payload.content,
          date: payload.date,
          title: payload.title,
        })
        .then(() => {
          store.dispatch(Constant.GET_NOTICELIST);
        })
        .catch((exp) => alert("공지사항 수정 처리에 실패하였습니다." + exp));
    },

    // 공지 삭제
    [Constant.REMOVE_NOTICE]: (store, payload) => {
      http
        .delete("/bo-api/notice/" + payload.noticeId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_NOTICELIST);
        })
        .catch((exp) => alert("공지사항 삭제 처리에 실패하였습니다." + exp));
    },
  },

  mutations: {
    [Constant.GET_NOTICELIST]: (state, payload) => {
      state.notices = payload.notices;
    },
    [Constant.GET_NOTICE]: (state, payload) => {

      state.notice = payload.notice;
    },
  },

  modules: {},
};

export default noticestore;
