<template>
  <v-app id="inspire">
    <div id="logo">
      <v-img
        style="max-width:150px;"
        src="@/assets/image/lg_uplus_logo.png"
      ></v-img>
    </div>
    <div id="loginBlock" fit-height>
      <v-layout align-center justify-center id="layout">
        <v-flex xs12 sm8 md4>
          <v-alert v-if="loginFail" dense outlined type="error">
            <strong>ID</strong>와 <strong>비밀번호</strong>를 다시 확인해주세요.
          </v-alert>
          <v-card class="elevation-12">
            <v-toolbar color="#363636" dark flat>
              <v-toolbar-title>Admin Login</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <v-form>
                <v-row>
                  <v-icon style="margin-right:10px;">mdi-account</v-icon>
                  <v-text-field
                    label="ID"
                    name="ID"
                    type="text"
                    v-model="inputId"
                    id="idInput"
                  ></v-text-field>
                </v-row>
                <v-row>
                  <v-icon style="margin-right:10px;">mdi-lock</v-icon>
                  <v-text-field
                    label="Password"
                    name="password"
                    type="password"
                    v-model="inputPwd"
                    id="pwdInput"
                  ></v-text-field>
                </v-row>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                dark
                color="#363636"
                style="width:100%;"
                @click="login"
                id="loginBtn"
              >
                Login
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </div>
  </v-app>
</template>

<script>
import Constant from "../../Constant";

export default {
  data() {
    return {
      drawer: null,

      inputId: "",
      inputPwd: "",

    };
  },
  computed: {
    loginFail() {
      return this.$store.state.loginstore.loginFail;
    },
  },
  methods: {
    login() {
      if (this.inputId == "") {
        alert("아이디를 입력해주세요.");
      } else if (this.inputPwd == "") {
        alert("비밀번호를 입력해주세요.");
      } else {
        //로그인 진행
        this.$store.dispatch(Constant.LOGIN, {
          name: this.inputId,
          pwd: this.inputPwd,
        });

        // 
      }
    },
  },
  mounted() {
    var idInput = document.getElementById("idInput");
    var pwdInput = document.getElementById("pwdInput");
    idInput.addEventListener("keyup", function(event) {
      if (event.keyCode === 13) {
        event.preventDefault();
        document.getElementById("loginBtn").click();
      }
    });
    pwdInput.addEventListener("keyup", function(event) {
      if (event.keyCode === 13) {
        event.preventDefault();
        document.getElementById("loginBtn").click();
      }
    });
  },
};
</script>

<style scoped>
#layout {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#content {
  width: 100vw;
  height: 100vh;
  margin: 0px;
  overflow: hidden;
}
::-webkit-scrollbar {
  display: none;
}

#loginBlock {
  height: 60vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
}
#logo {
  margin-top: 8vh;
  height: 20vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
</style>
