<template>
  <div id="app">
    <v-app id="inspire">
      <v-data-table :headers="headers" :items="infos" class="elevation-1">
        <template v-slot:[`item.validate`]="{ item }">
          {{ item.validate.substring(0, 10) }}
        </template>
        <template v-slot:[`item.price`]="{ item }">
          {{ item.price }} 원
        </template>
        <template v-slot:[`item.used`]="{ item }">
          <div v-if="item.used === true">
            사용완료
          </div>
          <div v-else>
            미사용
          </div>
        </template>

        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>할인권 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  할인권 생성
                </v-btn>
              </template>

              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-if="dontmodify"
                          readonly
                          v-model="editedItem.dcNumber"
                          label="할인권 번호 (필수)"
                        ></v-text-field>
                        <v-text-field
                          v-else
                          v-model="editedItem.dcNumber"
                          label="할인권 번호 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.price"
                          label="금액 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <span> 유효기간 (필수)</span>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-date-picker
                          v-model="editedItem.validate"
                          label="유효기간"
                        ></v-date-picker>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    취소
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    저장
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">삭제하시겠습니까?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >삭제</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">
            mdi-delete
          </v-icon>
        </template>

        <template v-slot:no-data>
          <h1>데이터가 없습니다.</h1>
        </template>
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import Constant from "../../Constant";

export default {
  data() {
    return {
      picker: new Date().toISOString().substr(0, 10),
      dialog: false,
      dialogDelete: false,
      detaildialog: false,
      headers: [
        {
          text: "할인권번호",
          align: "start",
          // sortable: false,
          value: "dcNumber",
          width: "20%",
        },
        { text: "금액", value: "price", width: "30%" },
        { text: "사용여부", value: "used", width: "15%" },
        { text: "유효기간", value: "validate", width: "25%" },
        { text: "수정/삭제", value: "actions", width: "10%" },
      ],
      editedIndex: -1,
      editedDcNumber: "",
      editedItem: {
        dcNumber: "",
        price: 0,
        used: false,
        validate: "",
      },
      defaultItem: {
        dcNumber: "",
        price: 0,
        used: false,
        validate: "",
      },

      dontmodify: false,
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "할인권 생성" : "할인권 수정";
    },
    infos() {
      return this.$store.state.discountcouponstore.dcs;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    detaildialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.$store.dispatch(Constant.GET_DCLIST);
  },

  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.dontmodify = true;
      this.editedIndex = this.infos.indexOf(item);
      this.editedDcNumber = item.dcNumber;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedDcNumber = item.dcNumber;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$store.dispatch(Constant.REMOVE_DC, {
        dcNumber: this.editedDcNumber,
      });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.detaildialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });

      this.dontmodify = false;
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    //저장 아니면 수정
    save() {
      if (
        this.editedItem.dcNumber == "" ||
        this.editedItem.price == 0 ||
        this.editedItem.validate == ""
      ) {
        alert("필수입력사항을 입력하십시오.");
      } else {
        this.$store.dispatch(Constant.MODIFY_DC, {
          dcNumber: this.editedItem.dcNumber,
          price: this.editedItem.price,
          used: this.editedItem.used,
          validate: this.editedItem.validate+"T00:00:00",
        });
        this.dontmodify = false;

        this.close();
      }
    },
  },
};
</script>

<style scoped></style>
