import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
import axios from "axios";
Vue.use(Vuex);

const planstore = {
  state: {
    plans: [],
    plan: {},
  },

  actions: {

    //전체 요금제 리스트 가져오기
    [Constant.GET_PLANLIST]: (store) => {
      http
        .get("/bo-api/plan/")
        .then((response) => {
          store.commit(Constant.GET_PLANLIST, { plans: response.data });
        })
        .catch((exp) => alert("getPlanList처리에 실패하였습니다!!" + exp));
    },

    // 요금제 id로 요금제 하나 가져오기
    [Constant.GET_PLAN]: (store, payload) => {
      http
        .get("/bo-api/plan/" + payload.planId)
        .then((response) => {
          store.commit(Constant.GET_PLAN, { plan: response.data });
        })
        .catch((exp) => alert("getPlan처리에 실패하였습니다." + exp));
    },

    //요금제 수정
    [Constant.MODIFY_PLAN]: (store, payload) => {
      http
        .put("/bo-api/plan/", {
          planId: payload.planId,
          name: payload.name,
          call: payload.call,
          data: payload.data,
          message: payload.message,
          price: payload.price,
          share: payload.share,
          serviceList:payload.serviceList,

        })
        .then(() => {
          store.dispatch(Constant.GET_PLANLIST);
        })
        .catch((exp) => alert("요금제 수정 처리에 실패하였습니다." + exp));
    },

    // 요금제 삭제
    [Constant.REMOVE_PLAN]: (store, payload) => {
      http
        .delete("/bo-api/plan/" + payload.planId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_PLANLIST);
        })
        .catch((exp) => alert("요금제 삭제 처리에 실패하였습니다." + exp));
    },
   
  },

  mutations: {
    [Constant.GET_PLANLIST]: (state, payload) => {
      state.plans = payload.plans;
    },
    [Constant.GET_PLAN]: (state, payload) => {
      state.plan = payload.plan;
    },
  },

  modules: {},
};

export default planstore;
