import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const discountcouponstore = {
  state: {
    dcs: [],
    dc: {},
  },

  actions: {
    //전체 할인권 리스트 가져오기
    [Constant.GET_DCLIST]: (store) => {
      http
        .get("/bo-api/discountcoupon/")
        .then((response) => {
          store.commit(Constant.GET_DCLIST, { dcs: response.data });
        })
        .catch((exp) => alert("getDcList처리에 실패하였습니다!!" + exp));
    },

    // 할인권 id로 선물 하나 가져오기
    [Constant.GET_DC]: (store, payload) => {
      http
        .get("/bo-api/discountcoupon/" + payload.dcNumber)
        .then((response) => {
          store.commit(Constant.GET_DC, { dc: response.data });
        })
        .catch((exp) => alert("getDc처리에 실패하였습니다." + exp));
    },

    
    //할인권 수정
    [Constant.MODIFY_DC]: (store, payload) => {
      http
        .put("/bo-api/discountcoupon/", {
          dcNumber: payload.dcNumber,
          price: payload.price,
          used: payload.used,
          validate: payload.validate,
        })
        .then(() => {
          store.dispatch(Constant.GET_DCLIST);
        })
        .catch((exp) => alert("할인권 수정 처리에 실패하였습니다." + exp));
    },

    // 할인권 삭제
    [Constant.REMOVE_DC]: (store, payload) => {
      http
        .delete("/bo-api/discountcoupon/" + payload.dcNumber)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_DCLIST);
        })
        .catch((exp) => alert("할인권 삭제 처리에 실패하였습니다." + exp));
    },
    
  },

  mutations: {
    [Constant.GET_DCLIST]: (state, payload) => {
      for (let i = 0; i < payload.dcs.length; i++) {
        payload.dcs[i].validate = payload.dcs[i].validate.substring(0,10);
      }
      state.dcs = payload.dcs;
    },
    [Constant.GET_DC]: (state, payload) => {
      payload.dc.validate = payload.dc.validate.substring(0,10);

      state.dc = payload.dc;
    },
  },

  modules: {},
};

export default discountcouponstore;
