import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const giftstore = {
  state: {
    gifts: [],
    gift: {},
  },

  actions: {

    //전체 선물 리스트 가져오기
    [Constant.GET_GIFTLIST]: (store) => {
      http
        .get("/bo-api/gift/")
        .then((response) => {
          store.commit(Constant.GET_GIFTLIST, { gifts: response.data });
        })
        .catch((exp) => alert("getGiftList처리에 실패하였습니다!!" + exp));
    },

    // 선물 id로 선물 하나 가져오기
    [Constant.GET_GIFT]: (store, payload) => {
      http
        .get("/bo-api/gift/" + payload.giftId)
        .then((response) => {
          store.commit(Constant.GET_GIFT, { gift: response.data });
        })
        .catch((exp) => alert("getGift처리에 실패하였습니다." + exp));
    },

    

    // 선물 삭제
    [Constant.REMOVE_GIFT]: (store, payload) => {
      http
        .delete("/bo-api/gift/" + payload.giftId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_GIFTLIST);
        })
        .catch((exp) => alert("선물 삭제 처리에 실패하였습니다." + exp));
    },
  },

  mutations: {
    [Constant.GET_GIFTLIST]: (state, payload) => {
      state.gifts = payload.gifts;
    },
    [Constant.GET_GIFT]: (state, payload) => {
      state.gift = payload.gift;
    },
  },

  modules: {},
};

export default giftstore;
