import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const adstore = {
  state: {
    ads: [],
    ad: {},
  },

  actions: {

    //전체 광고 리스트 가져오기
    [Constant.GET_ADLIST]: (store) => {
      http
        .get("/bo-api/advertisement/")
        .then((response) => {
          store.commit(Constant.GET_ADLIST, { ads: response.data });
        })
        .catch((exp) => alert("getAdList처리에 실패하였습니다!!" + exp));
    },

    // 광고 id로 광고 하나 가져오기
    [Constant.GET_AD]: (store, payload) => {
      http
        .get("/bo-api/advertisement/" + payload.adId)
        .then((response) => {
          store.commit(Constant.GET_AD, { ad: response.data });
        })
        .catch((exp) => alert("getAd처리에 실패하였습니다." + exp));
    },

    

    // 광고 삭제
    [Constant.REMOVE_AD]: (store, payload) => {
      http
        .delete("/bo-api/advertisement/" + payload.advertisementId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_ADLIST);
        })
        .catch((exp) => alert("광고 삭제 처리에 실패하였습니다." + exp));
    },
  },

  mutations: {
    [Constant.GET_ADLIST]: (state, payload) => {
      state.ads = payload.ads;
    },
    [Constant.GET_AD]: (state, payload) => {
      state.ad = payload.ad;
    },
  },

  modules: {},
};

export default adstore;
