import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const storestore = {
  state: {
    stores: [],
    store: {},
  },

  actions: {
    //매장

    //전체 매장 리스트 가져오기
    [Constant.GET_STORELIST]: (store) => {
      http
        .get("/bo-api/store/")
        .then((response) => {
          store.commit(Constant.GET_STORELIST, { stores: response.data });
        })
        .catch((exp) => alert("getStoreList처리에 실패하였습니다!!" + exp));
    },

    // 매장 id로 매장 하나 가져오기
    [Constant.GET_STORE]: (store, payload) => {
      http
        .get("/bo-api/store/" + payload.storeId)
        .then((response) => {
          store.commit(Constant.GET_STORE, { store: response.data });
        })
        .catch((exp) => alert("getStore처리에 실패하였습니다." + exp));
    },

    //매장정보 수정
    [Constant.MODIFY_STORE]: (store, payload) => {
      http
        .put("/bo-api/store/", {
          storeId: payload.storeId,
          name: payload.name,
          lat: payload.lat,
          lng: payload.lng,
          address: payload.address,
          number: payload.number,
          rating: payload.rating,
        })
        .then(() => {
          store.dispatch(Constant.GET_STORELIST);
        })
        .catch((exp) => alert("매장 수정 처리에 실패하였습니다." + exp));
    },

    // 매장 삭제
    [Constant.REMOVE_STORE]: (store, payload) => {
      http
        .delete("/bo-api/store/" + payload.storeId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_STORELIST);
        })
        .catch((exp) => alert("매장 삭제 처리에 실패하였습니다." + exp));
    },
  },

  mutations: {
    [Constant.GET_STORELIST]: (state, payload) => {
      state.stores = payload.stores;
    },
    [Constant.GET_STORE]: (state, payload) => {
      state.store = payload.store;
    },
  },

  modules: {},
};

export default storestore;
