import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const registrationstore = {
  state: {
    registrations: [],
    registration: {},
  },

  actions: {
    //전체 가입 리스트 가져오기
    [Constant.GET_REGISTRATIONLIST]: (store) => {

      http
        .get("/bo-api/registration/")
        .then((response) => {
          store.commit(Constant.GET_REGISTRATIONLIST, {
            registrations: response.data,
          });
        })
        .catch((exp) =>
          alert("getRegistrationList처리에 실패하였습니다!!" + exp)
        );

    },

    // 가입 id로 가입 하나 가져오기
    [Constant.GET_REGISTRATION]: (store, payload) => {
      http
        .get("/bo-api/registration/" + payload.registrationId)
        .then((response) => {
          store.commit(Constant.GET_REGISTRATION, {
            registration: response.data,
          });
        })
        .catch((exp) => alert("getRegistration처리에 실패하였습니다." + exp));
    },

    //가입 수정
    [Constant.MODIFY_REGISTRATION]: (store, payload) => {

      http
        .put("/bo-api/registration/", {
          registrationId: payload.registrationId,
          address: payload.address,
          date: payload.date,
          deliveryDemand: payload.deliveryDemand,
          deliveryName: payload.deliveryName,
          deliveryPhone: payload.deliveryPhone,
          deviceColorId: payload.deviceColorId,
          giftId: payload.giftId,
          insuranceId: payload.insuranceId,
          newNumber: payload.newNumber,
          pickup: payload.pickup,
          pickupDate: payload.pickupDate,
          planId: payload.planId,
          serviceList: payload.serviceList,
          storeId: payload.storeId,
          userId: payload.userId,
          zipcode: payload.zipcode,
        })
        .then(() => {
          store.dispatch(Constant.GET_REGISTRATIONLIST);
        })
        .catch((exp) => alert("가입 수정 처리에 실패하였습니다." + exp));


    },

    // 가입 삭제
    [Constant.REMOVE_REGISTRATION]: (store, payload) => {

      http
        .delete("/bo-api/registration/" + payload.registrationId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_REGISTRATIONLIST);
        })
        .catch((exp) => alert("가입 삭제 처리에 실패하였습니다." + exp));

    },

    //디바이스로 찾기
    [Constant.SEARCH_REGISTRATION_BY_DEVICE]: (store, payload) => {

      http
        .get("/bo-api/registration/device/" + payload.deviceId)
        .then((response) => {
          store.commit(Constant.GET_REGISTRATIONLIST, { registrations: response.data });
        })
      .catch(exp => alert('search by device 처리에 실패하였습니다.' + exp));

    },
    //요금제로 찾기
    [Constant.SEARCH_REGISTRATION_BY_PLAN]: (store, payload) => {
      http
        .get("/bo-api/registration/plan/" + payload.planId)
        .then((response) => {
          store.commit(Constant.GET_REGISTRATIONLIST, { registrations: response.data });
        })
      .catch(exp => alert('search by plan 처리에 실패하였습니다.' + exp));
    },
    //부가서비스로 찾기
    [Constant.SEARCH_REGISTRATION_BY_SERVICE]: (store, payload) => {
      http
        .get("/bo-api/registration/service/" + payload.serviceId)
        .then((response) => {
          store.commit(Constant.GET_REGISTRATIONLIST, { registrations: response.data });
        })
      .catch(exp => alert('search by service 처리에 실패하였습니다.' + exp));
    },
  },

  mutations: {
    [Constant.GET_REGISTRATIONLIST]: (state, payload) => {
      state.registrations = payload.registrations;
    },
    [Constant.GET_REGISTRATION]: (state, payload) => {
      state.registration = payload.registration;
    },
  },

  modules: {},
};

export default registrationstore;
