import Vuex from "vuex";
import Vue from "vue";
import router from "../../router";

import Constant from "../../Constant.js";
import log from "../../login-common.js";
Vue.use(Vuex);

const loginstore = {
  state: {
    loginId: "",
    token: "",
    type: "",
    loginFail: false,
    openSnackbar: false,
    logined: false,
    // logining: false,
    errorMessage: "",
  },

  actions: {
    //로그인
    [Constant.LOGIN]: (store, payload) => {
      log
        .post("/bo-api/admin/login", {
          name: payload.name,
          pwd: payload.pwd,
        })
        .then((response) => {
          //start
          if (response.data.tokenType != "error message") {
            window.localStorage.setItem("token", response.data.accessToken);
            window.localStorage.setItem("type", response.data.tokenType);
            window.localStorage.setItem("loginId", payload.name);
            store.commit(Constant.LOGINERROR, {
              errorMessage: "",
              loginId: payload.name,
              openSnackbar: true,
              loginFail: false,
              logined: true,
            });

            location.reload();

            
          }
          // 에러 -> 로컬스토리지 클리어
          else {
            window.localStorage.setItem("token", "");
            window.localStorage.setItem("type", "");
            window.localStorage.setItem("loginId", "");

            store.commit(Constant.LOGINERROR, {
              errorMessage: response.data.accessToken,
              loginFail: true,
              loginId: "",
              openSnackbar: false,
              logined: false
            });
          }
          // end
        })
        .catch((exp) => {
          alert(" 로그인 요청 처리에 실패하였습니다." + exp);
        });
    },

    [Constant.LOGOUT]: (store) => {
      window.localStorage.setItem("token", "");
      window.localStorage.setItem("type", "");
      window.localStorage.setItem("loginId", "");

      store.commit(Constant.LOGINERROR, {
        errorMessage: "",
        loginFail: false,
        loginId: "",
        openSnackbar: false,
        logined:false
      });

      alert("정상적으로 로그아웃되었습니다.");
      router.push({ path: "/" });
    },
  },

  mutations: {
    [Constant.LOGINERROR]: (state, payload) => {
      state.errorMessage = payload.errorMessage;
      state.loginFail = payload.loginFail;
      state.openSnackbar = payload.openSnackbar;
      state.loginId = payload.loginId;
      state.logined = payload.logined;

    },
  },

  modules: {},
};

export default loginstore;
