import Vuex from "vuex";
import Vue from "vue";

import Constant from "../../Constant.js";
import http from "../../http-common.js";
Vue.use(Vuex);

const insurancestore = {
  state: {
    insurances: [],
    insurance: {},
  },

  actions: {
    //보험

    //전체 보험 리스트 가져오기
    [Constant.GET_INSURANCELIST]: (store) => {
      http
        .get("/bo-api/insurance/")
        .then((response) => {
          store.commit(Constant.GET_INSURANCELIST, {
            insurances: response.data,
          });
        })
        .catch((exp) => alert("getInsuranceList처리에 실패하였습니다!!" + exp));
    },

    // 보험 id로 보험 하나 가져오기
    [Constant.GET_INSURANCE]: (store, payload) => {
      http
        .get("/bo-api/insurance/" + payload.insuranceId)
        .then((response) => {
          store.commit(Constant.GET_INSURANCE, { insurance: response.data });
        })
        .catch((exp) => alert("getInsurance처리에 실패하였습니다." + exp));
    },

    //보험 수정
    [Constant.MODIFY_INSURANCE]: (store, payload) => {
      http
        .put("/bo-api/insurance/", {
          insuranceId: payload.insuranceId,
          name: payload.name,
          price: payload.price,
          limit: payload.limit,
          description: payload.description,
        })
        .then(() => {
          store.dispatch(Constant.GET_INSURANCELIST);
        })
        .catch((exp) => alert("보험 수정 처리에 실패하였습니다." + exp));
    },

    // 보험 삭제
    [Constant.REMOVE_INSURANCE]: (store, payload) => {
      http
        .delete("/bo-api/insurance/" + payload.insuranceId)
        .then(() => {
          alert("삭제하였습니다.");
          store.dispatch(Constant.GET_INSURANCELIST);
        })
        .catch((exp) => alert("약관 삭제 처리에 실패하였습니다." + exp));
    },
  },

  mutations: {
    [Constant.GET_INSURANCELIST]: (state, payload) => {
      state.insurances = payload.insurances;
    },
    [Constant.GET_INSURANCE]: (state, payload) => {
      state.insurance = payload.insurance;
    },
  },

  modules: {},
};

export default insurancestore;
