import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

//약관
import Terms from "../components/terms/terms.vue";

//광고
import Ad from "../components/ad/ad.vue";

//공지사항
import Notice from "../components/notice/notice.vue";

//기기
import Device from "../components/device/device.vue";

//할인권
import Discountcoupon from "../components/discountcoupon/discountcoupon.vue";

//보험
import Insurance from "../components/insurance/insurance.vue";

//요금제
import Plan from "../components/plan/plan.vue";

//매장
import Store from "../components/store/store.vue";

//선물
import Gift from "../components/gift/gift.vue";

//부가서비스
import Service from "../components/service/service.vue";

//가입
import Registration from "../components/registration/registration.vue";

//로그인 디폴트
import DefaultView from "../components/defaultview.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: '/defaultView',
    name: "Home",
    component: Home
  },
  {
    path: "/defaultView",
    name: "DefaultView",
    component: DefaultView
  },
  {
    path: "/device",
    name: "Device",
    component: Device
  },
  {
    path: "/registration",
    name: "Registration",
    component: Registration
  },
  {
    path: "/discountcoupon",
    name: "Discountcoupon",
    component: Discountcoupon
  },
  {
    path: "/insurance",
    name: "Insurance",
    component: Insurance
  },
  {
    path: "/service",
    name: "Service",
    component: Service
  },
  {
    path: "/notice",
    name: "Notice",
    component: Notice
  },
  {
    path: "/plan",
    name: "Plan",
    component: Plan
  },
  {
    path: "/store",
    name: "Store",
    component: Store
  },
  {
    path: "/ad",
    name: "Ad",
    component: Ad
  },
  {
    path: "/gift",
    name: "Gift",
    component: Gift
  },
  {
    path: "/terms",
    name: "Terms",
    component: Terms,
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },

];

const router = new VueRouter({
  routes
});

export default router;
