<template>
  <div id="app">
    <v-app id="inspire">
      <v-data-table :headers="headers" :items="infos" class="elevation-1">
        <template v-slot:[`item.date`]="{ item }">
          {{ item.date.substring(0, 10) }}
        </template>
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>기기 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  기기 추가
                </v-btn>
              </template>

              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.name"
                          label="기기명 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.model"
                          label="모델명 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.manufacturer"
                          label="제조사 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <span>출시일 (필수)</span>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-date-picker
                          v-model="editedItem.date"
                          label="출시일"
                        ></v-date-picker>
                      </v-col>
                    </v-row>

                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.cpu"
                          label="CPU (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.display"
                          label="디스플레이 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.displayInch"
                          label="디스플레이 인치"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.camera"
                          label="카메라 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.battery"
                          label="배터리 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.memory"
                          label="메모리 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.size"
                          label="사이즈 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.weight"
                          label="무게 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.waterproof"
                          label="방수 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.price"
                          label="가격 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-textarea
                          v-model="editedItem.description"
                          label="상세설명"
                        ></v-textarea>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-file-input
                          label="설명이미지 (필수)"
                          v-model="editedItem.descriptionImage"
                          filled
                          prepend-icon="mdi-camera"
                        ></v-file-input>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-textarea
                          v-model="editedItem.storageDescription1"
                          label="저장공간 설명1 (필수)"
                        ></v-textarea>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-textarea
                          v-model="editedItem.storageDescription2"
                          label="저장공간 설명2 (필수)"
                        ></v-textarea>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    취소
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    저장
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">삭제하시겠습니까?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >삭제</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <!-- 상세보기 -->
            <v-dialog
              v-model="detaildialog"
              fullscreen
              hide-overlay
              transition="dialog-bottom-transition"
            >
              <template v-slot:activator="{}"> </template>
              <v-card>
                <!-- 상세 페이지 props -->
                <device-detail
                  :item="editedItem"
                  :colors="colors"
                  :name="editedItem.name"
                  @close-detail="closeDetail"
                />
                <!-- :dId="editedItem.deviceId" -->
              </v-card>
            </v-dialog>
            <!-- 삭제 모달 상세보기 end -->
            <!-- 디스크립션이미지보기 -->
            <v-dialog v-model="descriptiondialog" width="600px">
              <v-card>
                <v-card-title>
                  <span class="headline">설명 이미지</span>
                </v-card-title>
                <img
                  :src="editedItem.descriptionImage"
                  style="max-width:80vh;"
                />

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="green darken-1" text @click="close">
                    닫기
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <!-- 디스크립션이미지보기  end -->
          </v-toolbar>
        </template>
        <template v-slot:[`item.modify_actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
        </template>

        <template v-slot:[`item.delete_actions`]="{ item }">
          <v-icon small class="mr-2" @click="deleteItem(item)">
            mdi-delete
          </v-icon>
        </template>

        <template v-slot:[`item.detail_actions`]="{ item }">
          <v-icon small class="mr-2" @click="detailItem(item)">
            mdi-card-search-outline
          </v-icon>
        </template>

        <template v-slot:[`item.description_actions`]="{ item }">
          <v-icon small class="mr-2" @click="descriptionItem(item)">
            mdi-image-text
          </v-icon>
        </template>
        <template v-slot:no-data>
          <h1>데이터가 없습니다.</h1>
        </template>
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import Constant from "../../Constant";
import DeviceDetail from "./deviceDetail.vue";

export default {
  components: {
    DeviceDetail,
  },
  data() {
    return {
      dialog: false,
      detaildialog: false,
      descriptiondialog: false,
      dialogDelete: false,
      headers: [
        {
          text: "기기ID",
          align: "start",
          // sortable: false,
          value: "deviceId",
          width: "10%",
        },
        { text: "기기명", value: "name", width: "15%" },
        { text: "모델명", value: "model", width: "15%" },
        { text: "제조사", value: "manufacturer", width: "10%" },
        { text: "출시일자", value: "date", width: "10%" },
        { text: "수정", value: "modify_actions", width: "10%" },
        { text: "삭제", value: "delete_actions", width: "10%" },
        { text: "상세보기", value: "detail_actions", width: "10%" },
        { text: "설명이미지", value: "description_actions", width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        deviceId: 0,
        name: "",
        model: "",
        cpu: "",
        display: "",
        displayInch: 0.0,
        camera: "",
        battery: 0,
        memory: 0,
        size: "",
        weight: "",
        waterproof: "",
        description: "",
        descriptionImage: [],
        storageDescription1: "",
        storageDescription2: "",
        manufacturer: "",
        price: 0,
        date: "",
      },
      defaultItem: {
        deviceId: 0,
        name: "",
        model: "",
        cpu: "",
        display: "",
        displayInch: 0.0,
        camera: "",
        battery: 0,
        memory: 0,
        size: "",
        weight: "",
        waterproof: "",
        description: "",
        descriptionImage: [],
        storageDescription1: "",
        storageDescription2: "",
        manufacturer: "",
        price: 0,
        date: "",
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "기기 추가" : "기기 정보 수정";
    },
    infos() {
      return this.$store.state.devicestore.devices;
    },
    colors() {
      return this.$store.state.devicecolorstore.devicecolors;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    detaildialog(val) {
      val || this.close();
    },
    descriptiondialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.$store.dispatch(Constant.GET_DEVICELIST);
  },

  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;

      this.$store.dispatch(Constant.GET_DEVICECOLORLIST, {
        dId: item.deviceId,
      });
    },
    //디스크립션이미지 버튼 눌렀을 때
    descriptionItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.descriptiondialog = true;
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.editedIndex = item.deviceId;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = item.deviceId;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$store.dispatch(Constant.REMOVE_DEVICE, {
        deviceId: this.editedIndex,
      });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.descriptiondialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      //수정 메소드로 추가까지 가능
      if (
        this.editedItem.name == "" ||
        this.editedItem.model == "" ||
        this.editedItem.cpu == "" ||
        this.editedItem.display == "" ||
        this.editedItem.camera == "" ||
        this.editedItem.battery == 0 ||
        this.editedItem.memory == 0 ||
        this.editedItem.size == "" ||
        this.editedItem.weight == "" ||
        this.editedItem.waterproof == "" ||
        this.editedItem.storageDescription1 == "" ||
        this.editedItem.storageDescription2 == "" ||
        this.editedItem.manufacturer == "" ||
        this.editedItem.price == 0 ||
        this.editedItem.date == "" ||
        this.editedItem.descriptionImage.length == 0 
      ) {
        alert("필수입력사항을 입력하십시오.");
      } else {
        this.$store.dispatch(Constant.MODIFY_DEVICE, {
          deviceId: this.editedIndex,
          name: this.editedItem.name,
          model: this.editedItem.model,
          cpu: this.editedItem.cpu,
          display: this.editedItem.display,
          displayInch: this.editedItem.displayInch,
          camera: this.editedItem.camera,
          battery: this.editedItem.battery,
          memory: this.editedItem.memory,
          size: this.editedItem.size,
          weight: this.editedItem.weight,
          waterproof: this.editedItem.waterproof,
          manufacturer: this.editedItem.manufacturer,
          description: this.editedItem.description,
          descriptionImage: this.editedItem.descriptionImage,
          storageDescription1: this.editedItem.storageDescription1,
          storageDescription2: this.editedItem.storageDescription2,
          price: this.editedItem.price,
          date: this.editedItem.date + "T00:00:00",
        });

        this.close();
      }
    },

    closeDetail() {
      this.detaildialog = false;
    },
  },
};
</script>

<style scoped></style>
