<template>
  <div id="app">
    <v-app id="inspire">
      <v-data-table :headers="headers" :items="infos" class="elevation-1">
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>선물 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  선물 생성
                </v-btn>
              </template>

              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.name"
                          label="선물명 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-file-input
                          label="선물이미지 (필수)"
                          v-model="editedItem.image"
                          filled
                          prepend-icon="mdi-camera"
                        ></v-file-input>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    취소
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    저장
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">삭제하시겠습니까?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >삭제</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <!-- 상세보기 -->
            <v-dialog v-model="detaildialog" width="600px">
              <v-card>
                <v-card-title>
                  <span class="headline">선물 이미지</span>
                </v-card-title>
                <img
                  :src="editedItem.image"
                  style="max-width:80vh;"
                />

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="green darken-1" text @click="close">
                    닫기
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <!-- 삭제 모달 상세보기 end -->
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">
            mdi-delete
          </v-icon>
          <v-icon small class="mr-2" @click="detailItem(item)">
            mdi-card-search-outline
          </v-icon>
        </template>

        <template v-slot:no-data>
          <h1>데이터가 없습니다.</h1>
        </template>
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import Constant from "../../Constant";
import multi from "../../multi-common";

export default {
  data() {
    return {
      dialog: false,
      dialogDelete: false,
      detaildialog: false,
      headers: [
        {
          text: "ID",
          align: "start",
          // sortable: false,
          value: "giftId",
          width: "20%",
        },
        { text: "선물명", value: "name", width: "70%" },
        { text: "수정/삭제", value: "actions", width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        image: "",
        name: "",
      },
      defaultItem: {
        image: "",
        name: "",
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "선물 추가" : "선물 정보 수정";
    },
    infos() {
      return this.$store.state.giftstore.gifts;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    detaildialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.$store.dispatch(Constant.GET_GIFTLIST);
  },

  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.editedIndex = item.giftId;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = item.giftId;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$store.dispatch(Constant.REMOVE_GIFT, { giftId: this.editedIndex });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.detaildialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    //저장 , 수정
    async save() {
      if (
        this.editedItem.name == "" ||
        this.editedItem.image == ""
      ) {
        alert("필수입력사항을 입력하십시오.");
      } else {
        let fd = new FormData();
        fd.append("image", this.editedItem.image);
        fd.append("name", this.editedItem.name);
        fd.append("giftId", this.editedIndex);
        await multi
          .put("/bo-api/gift/", fd)
          .then(() => {
            console.log("SUCCESS!!");
          })
          .catch(function() {
            console.log("FAILURE!!");
          });

        this.$store.dispatch(Constant.GET_GIFTLIST);
        this.close();
      }
    },
  },
};
</script>

<style scoped></style>
