<template>
  <div id="app">
    <v-app id="inspire">
      <v-data-table :headers="headers" :items="infos" class="elevation-1">
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>전국 유플러스 매장 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  매장 생성
                </v-btn>
              </template>

              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.name"
                          label="매장명 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="6" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.lat"
                          label="위도 (필수)"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.lng"
                          label="경도 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.address"
                          label="주소 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.number"
                          label="전화번호 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.rating"
                          label="평점 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    취소
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    저장
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">삭제하시겠습니까?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >삭제</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">
            mdi-delete
          </v-icon>
        </template>

        <template v-slot:no-data>
          <h1>데이터가 없습니다.</h1>
        </template>
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import Constant from "../../Constant";

export default {
  data() {
    return {
      dialog: false,
      dialogDelete: false,
      detaildialog: false,
      headers: [
        {
          text: "매장ID",
          align: "start",
          // sortable: false,
          value: "storeId",
          width: "10%",
        },
        { text: "매장명", value: "name", width: "30%" },
        { text: "매장 위치 위도", value: "lat", width: "5%" },
        { text: "매장 위치 경도", value: "lng", width: "5%" },
        { text: "매장 주소", value: "address", width: "20%" },
        { text: "매장 전화번호", value: "number", width: "10%" },
        { text: "매장 평점", value: "rating", width: "10%" },
        { text: "수정/삭제", value: "actions", width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        name: "",
        lat: 0.0,
        lng: 0.0,
        address: "",
        number: "",
        rating: 0.0,
      },
      defaultItem: {
        name: "",
        lat: 0.0,
        lng: 0.0,
        address: "",
        number: "",
        rating: 0.0,
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "매장 생성" : "매장 정보 수정";
    },
    infos() {
      return this.$store.state.storestore.stores;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    detaildialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.$store.dispatch(Constant.GET_STORELIST);
  },

  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.editedIndex = item.storeId;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = item.storeId;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$store.dispatch(Constant.REMOVE_STORE, {
        storeId: this.editedIndex,
      });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.detaildialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    //저장 아니면 수정
    save() {
      if (
        this.editedItem.name == "" ||
        this.editedItem.lat == 0 ||
        this.editedItem.lng == 0 ||
        this.editedItem.address == "" ||
        this.editedItem.number == ""
      ) {
        alert("필수입력사항을 입력하십시오.");
      } 
      else if(this.editedItem.rating < 0 || this.editedItem.rating > 5){
        alert("평점을 0~5점 사이로 입력해주세요.");

      }
      else {
      this.$store.dispatch(Constant.MODIFY_STORE, {
        storeId: this.editedIndex,
        name: this.editedItem.name,
        lat: this.editedItem.lat,
        lng: this.editedItem.lng,
        address: this.editedItem.address,
        number: this.editedItem.number,
        rating: this.editedItem.rating,
      });
      this.close();
      }
    },
  },
};
</script>

<style scoped></style>
