<template>
  <div>
    <v-toolbar dark color="#363636">
      <v-toolbar-title>{{ name }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon dark @click="closeDetail">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-toolbar>
    <div class="deviceDetail">
      <div class="tit_wrap">
        <h3 class="h3_tit">기기 스펙정보</h3>
      </div>
      <div class="spec_area">
        <div class="img">
          <v-carousel :show-arrows="false" height="auto">
            <v-carousel-item
              v-if="trigger"
              :src="selectedFront"
            ></v-carousel-item>
            <v-carousel-item
              v-if="trigger"
              :src="selectedBack"
            ></v-carousel-item>
            <v-carousel-item
              v-if="!trigger"
              src="@/assets/image/devicecolor_default.jpg"
            ></v-carousel-item>
            <v-carousel-item
              v-if="!trigger"
              src="@/assets/image/devicecolor_default.jpg"
            ></v-carousel-item>
          </v-carousel>

          <v-row align="center">
            <v-col class="d-flex" cols="12" sm="12">

              <select v-model="selectedIndex" @change="changeColor">
                <option value="-1" selected disabled
                  >==색상을 선택하세요==</option
                >
                <option
                  v-for="(color, index) in colors"
                  :key="index"
                  :value="index"
                  >{{ color.name }}</option
                >
              </select>
            </v-col>
          </v-row>
        </div>
        <div class="spec_table">
          <table>
            <colgroup>
              <col style="width : 28%;" />
              <col />
            </colgroup>
            <tbody>
              <tr>
                <th scope="row">기기명</th>
                <td>{{ item.name }}</td>
              </tr>
              <tr>
                <th scope="row">모델명</th>
                <td>{{ item.model }}</td>
              </tr>
              <tr>
                <th scope="row">CPU</th>
                <td>{{ item.cpu }}</td>
              </tr>
              <tr>
                <th scope="row">디스플레이</th>
                <td>{{ item.display }}</td>
              </tr>
              <tr>
                <th scope="row">카메라</th>
                <td>{{ item.camera }}</td>
              </tr>
              <tr>
                <th scope="row">배터리</th>
                <td>{{ item.battery }}</td>
              </tr>
              <tr>
                <th scope="row">메모리</th>
                <td>{{ item.memory }}</td>
              </tr>
              <tr>
                <th scope="row">사이즈</th>
                <td>{{ item.size }}</td>
              </tr>
              <tr>
                <th scope="row">무게</th>
                <td>{{ item.weight }}</td>
              </tr>
              <tr>
                <th scope="row">방수</th>
                <td>{{ item.waterproof }}</td>
              </tr>
              <tr>
                <th scope="row">제조사</th>
                <td>{{ item.manufacturer }}</td>
              </tr>
              <tr>
                <th scope="row">가격</th>
                <td>{{ item.price }}</td>
              </tr>
              <tr>
                <th scope="row">출시일자</th>
                <td>{{ item.date.substring(0, 10) }}</td>
              </tr>
              <tr>
                <th scope="row">기기설명</th>
                <td>{{ item.description }}</td>
              </tr>
              <tr>
                <th scope="row">storagedescription1</th>
                <td>{{ item.storageDescription1 }}</td>
              </tr>
              <tr>
                <th scope="row">storagedescription2</th>
                <td>{{ item.storageDescription2 }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <!-- spec_table end -->

        <div style="clear:both;">
          <div class="tit_wrap">
            <h3 class="h3_tit">기기 색상정보</h3>
          </div>

          <!-- 디바이스 색상 테이블 -->
          <v-data-table :headers="headers" :items="colors" class="elevation-1">
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title>색상 관리</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      color="primary"
                      dark
                      class="mb-2"
                      v-bind="attrs"
                      v-on="on"
                    >
                      색상 추가
                    </v-btn>
                  </template>

                  <v-card>
                    <v-card-title>
                      <span class="headline">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col cols="12" sm="12" md="12">
                            <v-text-field
                              v-model="editedItem.name"
                              label="색상명 (필수)"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                        <v-row>
                          <v-col cols="12" sm="12" md="12">
                            <v-text-field
                              v-model="editedItem.hex"
                              label="색상코드 (필수)"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                        <v-row>
                          <v-col cols="12" sm="12" md="12">
                            <v-file-input
                              label="전면사진 (필수)"
                              v-model="editedItem.imageFront"
                              filled
                              prepend-icon="mdi-camera"
                            ></v-file-input>
                          </v-col>
                        </v-row>
                        <v-row>
                          <v-col cols="12" sm="12" md="12">
                            <v-file-input
                              label="후면사진 (필수)"
                              v-model="editedItem.imageBack"
                              filled
                              prepend-icon="mdi-camera"
                            ></v-file-input>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="close">
                        취소
                      </v-btn>
                      <v-btn color="blue darken-1" text @click="save">
                        저장
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>

                <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="headline"
                      >삭제하시겠습니까?</v-card-title
                    >
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete"
                        >취소</v-btn
                      >
                      <v-btn
                        color="blue darken-1"
                        text
                        @click="deleteItemConfirm"
                        >삭제</v-btn
                      >
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-toolbar>
            </template>
            <template v-slot:[`item.modify_actions`]="{ item }">
              <v-icon small class="mr-2" @click="editItem(item)">
                mdi-pencil
              </v-icon>
            </template>

            <template v-slot:[`item.delete_actions`]="{ item }">
              <v-icon small class="mr-2" @click="deleteItem(item)">
                mdi-delete
              </v-icon>
            </template>

            <template v-slot:no-data>
              <h1>데이터가 없습니다.</h1>
            </template>
          </v-data-table>

          <!-- 디바이스 색상 테이블 end -->
        </div>
      </div>
      <!-- spec_area end -->
    </div>
  </div>
</template>

<script>
import Constant from "../../Constant";
import multi from "../../multi-common";

export default {
  components: {
  },
  data() {
    return {
      selectedIndex: -1,
      selectedBack: "",
      selectedFront: "",
      trigger: false,

      //디바이스 컬러 테이블
      dialog: false,
      detaildialog: false,
      dialogDelete: false,
      headers: [
        {
          text: "색상ID",
          align: "start",
          // sortable: false,
          value: "colorId",
          width: "20%",
        },
        { text: "색상코드", value: "hex", width: "30%" },
        { text: "색상명", value: "name", width: "30%" },
        { text: "수정", value: "modify_actions", width: "10%" },
        { text: "삭제", value: "delete_actions", width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        hex: "",
        name: "",
        deviceId: 0,
        imageFront: [],
        imageBack: [],
      },
      defaultItem: {
        hex: "",
        name: "",
        deviceId: 0,
        imageFront: [],
        imageBack: [],
      },
    };
  },
  props: {
    colors: {
      type: Array,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
    item: {
      type: Object,
      required: true,
    },
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "색상 추가" : "색상 정보 수정";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.editedIndex = item.colorId;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = item.colorId;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$store.dispatch(Constant.REMOVE_DEVICECOLOR, {
        colorId: this.editedIndex,
        dId: this.item.deviceId,
      });

      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    //저장, 수정
    async save() {
      if (this.editedItem.hex == "" ||
      this.editedItem.name == "" ||
      this.editedItem.imageFront == "" ||
      this.editedItem.imageBack == "" 
      ) {
        alert("필수입력사항을 입력하십시오.");
      } else {
        // 수정
        let fd = new FormData();
        fd.append("front", this.editedItem.imageFront);
        fd.append("back", this.editedItem.imageBack);
        fd.append("hex", this.editedItem.hex);
        fd.append("name", this.editedItem.name);
        fd.append("deviceId", this.item.deviceId);
        fd.append("colorId", this.editedIndex);
        await multi
          .put("/bo-api/deviceColor/", fd)
          .then(() => {
            console.log("SUCCESS!!");
          })
          .catch(function() {
            console.log("FAILURE!!");
          });
        this.$store.dispatch(Constant.GET_DEVICECOLORLIST, {
          dId: this.item.deviceId,
        });
        this.close();
      }
    },

    changeColor() {
      this.selectedBack = this.colors[this.selectedIndex].imageBack;
      this.selectedFront = this.colors[this.selectedIndex].imageFront;
      this.trigger = true;
    },

    closeDetail() {
      this.selectedIndex = -1;
      this.trigger = false;
      this.$emit("close-detail");
    },
  },
};
</script>

<style scoped>
.spec_area .img {
  float: left;
  width: 40%;
  /* height: 50%; */
  text-align: center;
  /* margin-right: 5%; */
  margin-left: 5%;
  margin-top: 3%;
}

.spec_area .spec_table {
  float: right;
  width: 40%;
  /* height: 50%; */
  text-align: center;
  margin-right: 5%;
  /* margin-left: 5%; */
  margin-top: 3%;
}
.spec_table table {
  position: relative;
  width: 100%;
  border-top: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
}

table {
  /* width: 100%; */
  border-collapse: collapse;
  table-layout: fixed;
  border-spacing: 0;
  background-color: transparent;
  box-sizing: content-box;
}

table tr {
  height: 40px;
  border-bottom: 1px solid #d9d9d9;
}
table th {
  font-size: 14px;
  letter-spacing: -1px;
  font-weight: normal;
  word-break: break-all;
  word-wrap: break-word;
  background-color: #f6f6f6;
}

td {
  font-weight: normal;
  text-align: left;
  padding: 10px;
}

.tit_wrap {
  position: relative;
  padding-top: 0;
  line-height: 100%;
}

.h3_tit,
.tit_wrap .h3_tit {
  display: inline-block;
  font-size: 24px;
  line-height: 29px;
  overflow: hidden;
  word-break: break-all;
  padding-top: 16px;
  padding-left: 16px;
}

select {
  width: 100%;
  padding: 0.8em 0.5em;
  border: 1px solid #999;
  font-family: inherit;
  background: url("/assets/image/arrow_down.png") no-repeat 95% 50%;
  border-radius: 6px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

select::-ms-expand {
  display: none;
}
</style>
