<template>
  <div id="app">
    <v-app id="inspire">
      <v-data-table :headers="headers" :items="infos" class="elevation-1">
        <template v-slot:[`item.description`]="{ item }">
          <div v-if="item.description.length > 70">
            {{ item.description.substring(0, 70) }}...
          </div>
          <div v-else>
            {{ item.description }}
          </div>
        </template>

        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>보험 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  보험 생성
                </v-btn>
              </template>

              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.name"
                          label="보험명 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.price"
                          label="보험료 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.limit"
                          label="보상한도 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-textarea
                          v-model="editedItem.description"
                          label="보험상세내용 (필수)"
                        ></v-textarea>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    취소
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    저장
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">삭제하시겠습니까?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >삭제</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <!-- 상세보기 -->
            <v-dialog v-model="detaildialog" width="600px">
              <v-card>
                <v-card-title>
                  <span class="headline">보험상세내용</span>
                </v-card-title>
                <v-card-text>{{ editedItem.description }}</v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="green darken-1" text @click="close">
                    닫기
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <!-- 삭제 모달 상세보기 end -->
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">
            mdi-delete
          </v-icon>
          <v-icon small class="mr-2" @click="detailItem(item)">
            mdi-card-search-outline
          </v-icon>
        </template>

        <template v-slot:no-data>
          <h1>데이터가 없습니다.</h1>
        </template>
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import Constant from "../../Constant";

export default {
  data() {
    return {
      dialog: false,
      dialogDelete: false,
      detaildialog: false,
      headers: [
        {
          text: "보험ID",
          align: "start",
          // sortable: false,
          value: "insuranceId",
          width: "10%",
        },
        { text: "보험명", value: "name", width: "30%" },
        { text: "보험료", value: "price", width: "10%" },
        { text: "보상한도", value: "limit", width: "10%" },
        { text: "보험상세내용", value: "description", width: "30%" },
        { text: "수정/삭제/상세보기", value: "actions", width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        name: "",
        price: 0,
        limit: 0,
        description: "",
      },
      defaultItem: {
        name: "",
        price: 0,
        limit: 0,
        description: "",
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "보험 생성" : "보험정보 수정";
    },
    infos() {
      return this.$store.state.insurancestore.insurances;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    detaildialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.$store.dispatch(Constant.GET_INSURANCELIST);
  },

  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.editedIndex = item.insuranceId;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = item.insuranceId;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$store.dispatch(Constant.REMOVE_INSURANCE, {
        insuranceId: this.editedIndex,
      });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.detaildialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    //저장 아니면 수정
    save() {
      if (
        this.editedItem.name == "" ||
        this.editedItem.price == 0 ||
        this.editedItem.limit == 0 ||
        this.editedItem.description == ""
      ) {
        alert("필수입력사항을 입력하십시오.");
      } else {
        this.$store.dispatch(Constant.MODIFY_INSURANCE, {
          insuranceId: this.editedIndex,
          name: this.editedItem.name,
          price: this.editedItem.price,
          limit: this.editedItem.limit,
          description: this.editedItem.description,
        });
        this.close();
      }
    },
  },
};
</script>

<style scoped></style>
