<template>
  <div>
    <v-toolbar dark color="#363636">
      <v-toolbar-title>{{ item.userName }} 님</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon dark @click="closeDetail">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-toolbar>
    <div class="deviceDetail">
      <div class="tit_wrap">
        <h3 class="h3_tit">가입 상세정보</h3>
      </div>
      <br>

      <div class="spec_table">
        <table>
          <colgroup>
            <col style="width : 28%;" />
            <col />
          </colgroup>
          <tbody>
            <tr>
              <th scope="row">가입 번호</th>
              <td>{{ item.registrationId }}</td>
            </tr>
            <tr>
              <th scope="row">가입자 성명</th>
              <td>{{ item.userName }}</td>
            </tr>
            <tr>
              <th scope="row">주소(우편번호)</th>
              <td>{{ item.address }} ( {{item.zipcode}} )</td>
            </tr>
            <tr>
              <th scope="row">가입일자</th>
              <td>{{ item.date.substring(0, 10) }}</td>
            </tr>
            <tr>
              <th scope="row">기기</th>
              <td>{{ item.deviceName }}</td>
            </tr>
            <tr>
              <th scope="row">기기색상</th>
              <td>{{ item.colorName }}</td>
            </tr>
            <tr>
              <th scope="row">기기 수령방법</th>
              <td v-if="item.pickup == 1">방문 수령</td>
              <td v-else>배송 수령</td>
            </tr>
            <tr v-if="item.pickup == 1">
              <th scope="row">매장</th>
              <td>{{ item.storeName }}</td>
            </tr>
            <tr v-if="item.pickup == 1 && item.pickupDate != null">
              <th scope="row">방문수령일</th>
              <td>{{ item.pickupDate.substring(0, 10) }}</td>
            </tr>
            <tr v-if="item.pickup == 0">
              <th scope="row">배송 시 요구사항</th>
              <td>{{ item.deliveryDemand }}</td>
            </tr>
            <tr v-if="item.pickup == 0">
              <th scope="row">수령인</th>
              <td>{{ item.deliveryName }}</td>
            </tr>
            <tr v-if="item.pickup == 0">
              <th scope="row">연락 가능한 전화번호</th>
              <td>{{ item.deliveryPhone }}</td>
            </tr>

            <tr>
              <th scope="row">할부</th>
              <td>{{ item.dividePay }}</td>
            </tr>
            <tr>
              <th scope="row">선물</th>
              <td>{{ item.giftName }}</td>
            </tr>
            <tr>
              <th scope="row">보험</th>
              <td>{{ item.insuranceName }}</td>
            </tr>
            <tr>
              <th scope="row">신규 전화번호</th>
              <td>{{ item.newNumber }}</td>
            </tr>
            <tr>
              <th scope="row">요금제</th>
              <td>{{ item.planName }}</td>
            </tr>
            <tr>
              <th scope="row">부가서비스</th>
              <td>
                <ul>
                  <li v-for="(serv, index) in item.serviceList" :key="index">{{serv.name}}</li>
                </ul>
              </td>
            </tr>
            
          </tbody>
        </table>
        <br>
        <!-- spec_table end -->
      </div>
    </div>
    <!-- spec_area end -->
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {};
  },
  props: {
    item: {
      type: Object,
      required: true,
    },
  },

  methods: {
    closeDetail() {
      this.$emit("close-detail");
    },
  },
};
</script>

