  margin-top: 3%;
}

.spec_area .spec_table {
  float: right;
  width: 40%;
  /* height: 50%; */
  text-align: center;
  margin-right: 5%;
  /* margin-left: 5%; */
  margin-top: 3%;
}

.spec_table table {
  position: relative;
  width: 60%;
  margin-left: 5%;
  border-top: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
}

