<template>
  <div id="app">
    <v-app id="inspire">
      <v-data-table :headers="headers" :items="infos" class="elevation-1">
        <template v-slot:[`item.content`]="{ item }">
          <div v-if="item.content.length > 70">
            {{ item.content.substring(0, 70) }}...
          </div>
          <div v-else>
            {{ item.content }}
          </div>
        </template>

        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>요금제 관리</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  요금제 생성
                </v-btn>
              </template>

              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.name"
                          label="요금제명 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.price"
                          label="가격 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="6" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.call"
                          label="통화시간 (필수)"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.data"
                          label="데이터 (필수)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="6" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.message"
                          label="메시지 (필수)"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" sm="6" md="6">
                        <v-text-field
                          v-model="editedItem.share"
                          label="나눠쓰기"
                        ></v-text-field>
                      </v-col>
                    </v-row>

                    <!-- 부가서비스 선택 -->
                    <v-row justify="center">
                      <v-dialog
                        v-model="selectdialog"
                        scrollable
                        max-width="300px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn color="primary" dark v-bind="attrs" v-on="on">
                            부가서비스 선택
                          </v-btn>
                        </template>
                        <v-card>
                          <v-card-title>부가서비스 리스트</v-card-title>
                          <v-divider></v-divider>
                          <v-card-text>
                            <v-container fluid>
                              <v-checkbox
                                v-for="(item, index) in services"
                                :key="index"
                                v-model="editedItem.serviceList"
                                :label="item.name"
                                :value="item.serviceId"
                                multiple
                              ></v-checkbox>
                            </v-container>
                          </v-card-text>
                          <v-divider></v-divider>
                          <v-card-actions>
                            <v-btn
                              color="blue darken-1"
                              text
                              @click="cancelSelect"
                            >
                              취소
                            </v-btn>
                            <v-btn
                              color="blue darken-1"
                              text
                              @click="selectdialog = false"
                            >
                              저장
                            </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </v-row>
                    <!-- 부가서비스 선택 end -->
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    취소
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    저장
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <!-- 상세보기 -->
            <v-dialog v-model="detaildialog" width="600px">
              <v-card>
                <v-card-title>
                  <span class="headline">요금제에 추천될 부가서비스</span>
                </v-card-title>
                <ul>
                  <li v-for="(item, index) in plan.serviceList" :key="index">
                    {{ item.name }}
                  </li>
                </ul>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="green darken-1" text @click="close">
                    닫기
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <!-- 삭제 모달 상세보기 end -->

            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">삭제하시겠습니까?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >취소</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >삭제</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">
            mdi-delete
          </v-icon>
        </template>
        <template v-slot:[`item.service_list`]="{ item }">
          <v-icon small class="mr-2" @click="detailItem(item)">
            mdi-format-list-bulleted-square
          </v-icon>
        </template>

        <template v-slot:no-data>
          <h1>데이터가 없습니다.</h1>
        </template>
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import Constant from "../../Constant";

export default {
  data() {
    return {
      selectdialog: false,
      dialog: false,
      dialogDelete: false,
      detaildialog: false,

      headers: [
        {
          text: "ID",
          align: "start",
          // sortable: false,
          value: "planId",
          width: "5%",
        },
        { text: "요금제명", value: "name", width: "15%" },
        { text: "통화시간", value: "call", width: "20%" },
        { text: "데이터", value: "data", width: "15%" },
        { text: "메시지", value: "message", width: "10%" },
        { text: "나눠쓰기", value: "share", width: "10%" },
        { text: "가격", value: "price", width: "5%" },
        { text: "수정", value: "actions", width: "10%" },
        { text: "부가서비스 리스트", value: "service_list", width: "10%" },
      ],
      editedIndex: -1,
      editedItem: {
        name: "",
        call: "",
        data: "",
        message: "",
        serviceList: [],
        share: "",
        price: 0,
      },
      defaultItem: {
        name: "",
        call: "",
        data: "",
        message: "",
        serviceList: [],
        share: "",
        price: 0,
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "요금제 추가" : "요금제 정보 수정";
    },
    infos() {
      return this.$store.state.planstore.plans;
    },
    services() {
      return this.$store.state.servicestore.services;
    },
    plan() {
      return this.$store.state.planstore.plan;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    detaildialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.$store.dispatch(Constant.GET_PLANLIST);
    this.$store.dispatch(Constant.GET_SERVICELIST);
  },

  methods: {
    //상세보기 버튼 눌렀을 때
    detailItem(item) {
      this.editedIndex = this.infos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.detaildialog = true;

      this.$store.dispatch(Constant.GET_PLAN, { planId: item.planId });
    },
    //수정 아이콘 눌렀을 때
    editItem(item) {
      this.editedIndex = item.planId;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //삭제 아이콘 눌렀을 때
    deleteItem(item) {
      this.editedIndex = item.planId;
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$store.dispatch(Constant.REMOVE_PLAN, { planId: this.editedIndex });
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.detaildialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    //저장 , 수정
    save() {
      if (this.editedItem.name == "" 
      || this.editedItem.call == ""
      || this.editedItem.data == ""
      || this.editedItem.message == ""
      || this.editedItem.price == 0
      ) {
        alert("필수입력사항을 입력하십시오.");
      } else {
        if(typeof this.editedItem.serviceList == "undefined"){
          this.editedItem.serviceList = [];
        }


        this.$store.dispatch(Constant.MODIFY_PLAN, {
          planId: this.editedIndex,
          name: this.editedItem.name,
          call: this.editedItem.call,
          data: this.editedItem.data,
          message: this.editedItem.message,
          share: this.editedItem.share,
          price: this.editedItem.price,
          serviceList: this.editedItem.serviceList,
        });
        this.close();
      }
    },
    cancelSelect() {
      this.editedItem.serviceList = [];
      this.selectdialog = false;
    },
  },
};
</script>

<style scoped></style>
